import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import ProfitsItem from '../FormItem/financingItem.jsx'
import InvestItem from '../FormItem/investItem.jsx'
import BenefitsItem from '../FormItem/benefitsItem.jsx'

export class StockForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.actionType == "detail" ? true : false,
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "key": "",
            "title": "",
            "category": "",
            "stockType": "",
            "type": "",        //投资类型
            "industry": "",
            "region": "",
            "companyProfits": {
                "isNeed": "0",
                "number": ""
            },
            invest: {
                min: "",
                max: ""
            },
            "estimateBenefits": {
                "times": "",
                "type": "1"
            },
            "needCost": "1",
            "detail": "",
            "accessory": {
                "name": "",
                "link": ""
            }
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/delegateFund/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function(data) {
                if(data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }
    companyProfitsCheck(rule, value, callback) {
        if ((value.isNeed == "0") || (value.isNeed > 0 && value.number)) {
            callback();
            return;
        }
        callback('请输入去年利润金额!');
    }

    investCheck(rule, value, callback) {
        if(!value.min|| !value.max) {
            callback('请输入单笔投资区间!');
        } else {
            if(value.max < value.min) {
                callback("投资上限必须大于下限!")
            }
            callback();
            return;
        }
    }

    estimateBenefitsCheck(rule, value, callback) {
        if (value.times) {
            callback();
            return;
        }
        callback('请输入投资估值!');
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/delegateFund/edit",
                    method: "post",
                    data: this.props.actionType == "edit" ? Object.assign(values, {id: this.props.infoID}) : values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("保存成功!", 1, function() {
                                hashHistory.push("/delegateFund")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金标题"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("title", {
                            rules: [{required: true, message: "请输入标题!"}],
                            initialValue: this.state.title
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金类别"
                    >
                        {getFieldDecorator("category", {
                            initialValue: "2"
                        })(
                            <Select disabled>
                                <Option value="1">债权</Option>
                                <Option value="2">股权</Option>
                                <Option value="3">资金收购</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="参/控股"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("stockType", {
                            rules: [{
                                required: true,
                                message: "请选择类型!"
                            }],
                            initialValue: this.state.stockType
                        })(
                            <RadioGroup disabled={this.state.disabled} >
                                <Radio value="1">参股型投资</Radio>
                                <Radio value="2">控股型投资</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资类型"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("type", {
                            rules: [{
                                required: true,
                                message: "请选择投资类型!"
                            }],
                            initialValue: this.state.type
                        })(
                            <Select disabled={this.state.disabled}>
                                {
                                    this.state.assetsType.stock.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资行业"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("industry", {
                            rules: [{
                                required: true,
                                message: "请选择投资行业!"
                            }],
                            initialValue: this.state.industry
                        })(
                            <Select disabled={this.state.disabled}>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资地区"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("region", {
                            rules: [{
                                required: true,
                                message: "请选择投资地区!"
                            }],
                            initialValue: this.state.region
                        })(
                            <Cascader disabled={this.state.disabled}
                                      options={this.state.cascaderRegion}
                            />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="标的企业去年净利润"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("companyProfits", {
                            rules: [{
                                validator: this.companyProfitsCheck
                            }],
                            initialValue: {
                                isNeed: this.state.companyProfits.isNeed,
                                number: this.state.companyProfits.number }
                        })(<ProfitsItem disabled={this.state.disabled} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="单笔投资区间"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("invest", {
                            rules: [{
                                validator: this.investCheck
                            }],
                            initialValue: {
                                min: this.state.invest.min,
                                max: this.state.invest.max
                            }
                        })(<InvestItem disabled={this.state.disabled} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资估值倍数"
                        hasFeedback={!this.state.disabled}
                    >

                        {getFieldDecorator("estimateBenefits", {
                            rules: [{
                                validator: this.estimateBenefitsCheck
                            }],
                            initialValue: {
                                times: this.state.estimateBenefits.times,
                                type: this.state.estimateBenefits.type
                            }
                        })(<BenefitsItem disabled={this.state.disabled} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="前期费用"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("needCost", {
                            rules: [{
                                required: true,
                                message: "请设置前期费用!"
                            }],
                            initialValue: this.state.needCost
                        })(
                            <RadioGroup disabled={this.state.disabled} >
                                <Radio value="1">有(评估/担保等)</Radio>
                                <Radio value="0">没有</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金详情"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("detail", {
                            rules: [{required: true, message: "请输入资金详情!"}],
                            initialValue: this.state.detail
                        })(
                            <TextArea disabled={this.state.disabled} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="附件"
                    >
                        <span className="ant-form-text"><a href={this.state.accessory.link}>{this.state.accessory.name}</a>
                        </span>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>立即发布</Button> : ""
                        }
                        {
                            !this.state.disabled ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedStockForm = Form.create({})(StockForm);

module.exports = WrappedStockForm;


