import React,{Component} from 'react'
import { Input, Radio } from 'antd';
const RadioGroup = Radio.Group;
class FinancingItem extends React.Component {
    constructor(props) {
        super(props);

        const value = this.props.value || {};
        this.state = {
            isNeed: value.number || '0',
            number: value.currency || 0
        };
        ['handleRadioChange', 'handleNumberChange', 'triggerChange'].forEach(m=>this[m] = this[m].bind(this))
    }
    componentWillReceiveProps(nextProps) {
        // Should be a controlled component.
        if ('value' in nextProps) {
            const value = nextProps.value;
            this.setState(value);
        }
    }

    handleRadioChange(e) {
        const isNeed = e.target.value;
        let number = isNeed == "0" ? 0 : this.state.number;
        if (!('value' in this.props)) {
            this.setState({
                isNeed,
                number
            });
        }
        this.triggerChange({ isNeed, number });
    }

    handleNumberChange(e) {
        const number = parseInt(e.target.value || 0, 10);
        if (isNaN(number)) {
            return;
        }
        if (!('value' in this.props)) {
            this.setState({ number });
        }
        this.triggerChange({ number });
    }

    triggerChange (changedValue) {
        // Should provide an event to pass value to Form.
        const onChange = this.props.onChange;
        if (onChange) {
            onChange(Object.assign({}, this.state, changedValue));
        }
    }

    render() {
        return (
            <RadioGroup value={this.state.isNeed} onChange={this.handleRadioChange} disabled={this.props.disabled} >
                <Radio value="0">无要求</Radio>
                <Radio value="1">
                    <Input value={this.state.number} onChange={this.handleNumberChange} disabled={this.props.disabled || this.state.isNeed == "0"} />万元(以上)
                </Radio>
            </RadioGroup>
        );
    }
}

module.exports = FinancingItem;


